#TestimonialHeader{
    margin-top: 34px;
    margin-bottom: 50px;
    margin-left: 12rem;
    width: 455px;
    height: 65px;


    font: var(--unnamed-font-style-normal) normal var(--unnamed-font-weight-bold) 65px/var(--unnamed-line-spacing-72) var(--unnamed-font-family-acumin-pro);
    color: var(--unnamed-color-373737);
    text-align: left;
    font: normal normal bold 65px/72px Acumin Pro;
    letter-spacing: 1.04px;
    color: #373737;
    opacity: 1;
}

#subHeader{
    padding-top: 0px;

    font: var(--unnamed-font-style-normal) normal var(--unnamed-font-weight-bold) var(--unnamed-font-size-40)/var(--unnamed-line-spacing-44) var(--unnamed-font-family-acumin-pro);
    letter-spacing: var(--unnamed-character-spacing-0-64);
    color: var(--unnamed-color-ffb347);
    font: normal normal bold 40px/44px Acumin Pro;
    letter-spacing: 0.64px;
    color: #FFB347;
    opacity: 1;
}

#Body{
    padding-left: 0px;
    padding-right: 0px;
    padding-bottom: 0px;
    margin-left: 12rem;
    width: 70%;

    font: var(--unnamed-font-style-normal) normal medium 20px/var(--unnamed-line-spacing-29) var(--unnamed-font-family-acumin-pro);
    color: var(--unnamed-color-979797);
    text-align: left;
    font: normal normal medium 20px/29px Acumin Pro;
    letter-spacing: 0.32px;
    color: #979797;
    opacity: 1;
}

#centerBlockShadow{
    margin-bottom: 100px;
    padding: 50px;
    background-color: #9FC0DE;
    width: 70%;

    font: var(--unnamed-font-style-normal) normal medium var(--unnamed-font-size-30)/34px var(--unnamed-font-family-acumin-pro);
    letter-spacing: var(--unnamed-character-spacing-0-48);
    color: var(--unnamed-color-f6f6f6);
    text-align: center;
    font: normal normal medium 30px/34px Acumin Pro;
    letter-spacing: 0.48px;
    color: #F6F6F6;
    opacity: 1;

    -webkit-box-shadow: 10px 15px 0px 7px #373737; 
    box-shadow: 10px 15px 0px 7px #373737;
}

#quoteSayer{
    font: var(--unnamed-font-style-normal) normal var(--unnamed-font-weight-bold) var(--unnamed-font-size-30)/var(--unnamed-line-spacing-44) var(--unnamed-font-family-acumin-pro);
    letter-spacing: var(--unnamed-character-spacing-0-48);
    color: var(--unnamed-color-373737);
    text-align: center;
    font: normal normal bold 30px/44px Acumin Pro;
    letter-spacing: 0.48px;
    color: #373737;
    opacity: 1;
}